import { BULLETS, BULLET_TYPE } from '@/constants';
import { vec2 } from 'gl-matrix';

export default class Bullet {
  type: BULLET_TYPE;
  x: number;
  y: number;
  directionVec: vec2;
  start: vec2;
  hue: number;
  velocity: vec2;
  length: number;
  bulletVec: vec2;
  end: vec2;
  damage: number;

  constructor({ ctx, x, y, directionVec, damage }: {
    ctx: CanvasRenderingContext2D,
    x: number;
    y: number;
    directionVec: vec2;
    damage: number;
  }) {
    this.type = BULLETS.LINE;
    this.x = x;
    this.y = y;
    this.directionVec = directionVec;

    // {vec2} this.start 
    this.start = vec2.fromValues(x, y);

    this.hue = 200;

    // {vec2} this.velocity bullet
    // 
    this.velocity = vec2.create();
    vec2.scale(this.velocity, directionVec, 2);

    // bullet
    this.length = 10;
    // bullet
    this.bulletVec = vec2.create();
    vec2.scale(this.bulletVec, directionVec, this.length);

    // {vec2} this.end 
    this.end = vec2.create();
    this.end = vec2.add(this.end, this.start, this.bulletVec);

    this.damage = damage || 5;
  }

  draw(ctx: CanvasRenderingContext2D) {
    // bullet
    vec2.add(this.start, this.start, this.velocity);
    vec2.add(this.end, this.end, this.velocity);

    // 
    ctx.save();
    ctx.strokeStyle = 'hsl(' + this.hue + ', 100%, 80%)';
    ctx.beginPath();
    ctx.moveTo(this.start[0], this.start[1]);
    ctx.lineTo(this.end[0], this.end[1]);
    ctx.stroke();
    ctx.restore();
  }
}



